
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
components: {
Group,
Cell
},
data () {
return {
data: []
};
},
mounted () {
let url = '/api/jifeidaoqitixing/json/108';
this.$http.get(url).then(({data}) => {
this.data = data.data;
});
}
};



